.hotel-view{flex:1}.gallery{display:flex}.gallery__photo{width:100%;display:block}.overview{display:flex;align-items:center;border-bottom:var(--line)}.overview__stars{margin-right:auto;display:flex}.overview__icon-star,.overview__icon-location{width:1.75rem;height:1.75rem;fill:var(--color-primary)}.overview__location{font-size:1.2rem;display:flex;align-items:center}.overview__icon-location{margin-right:.5rem}.overview__rating{background-color:var(--color-primary);color:#fff;margin-left:3rem;padding:0 2.25rem;align-self:stretch;display:flex;flex-direction:column;align-items:center;justify-content:center}@media only screen and (max-width: 37.5rem){.overview__rating{padding:0 1.25rem}}.overview__rating-average{font-size:2.5rem;font-weight:300;margin-bottom:-3px}@media only screen and (max-width: 37.5rem){.overview__rating-average{font-size:1.8rem}}.overview__rating-count{font-size:.8rem;text-transform:uppercase}@media only screen and (max-width: 37.5rem){.overview__rating-count{font-size:.5rem}}.detail{font-size:1.4rem;display:flex;padding:4.5rem;background-color:var(--color-grey-light-1);border-bottom:var(--line)}@media only screen and (max-width: 56.25em){.detail{padding:3rem}}@media only screen and (max-width: 37.5rem){.detail{flex-direction:column}}.description{background-color:#fff;box-shadow:var(--shadow-light);padding:3rem;flex:0 0 60%;margin-right:4.5rem}@media only screen and (max-width: 56.25em){.description{padding:2rem;margin-right:3rem}}@media only screen and (max-width: 37.5rem){.description{margin-right:0;margin-bottom:3rem}}.user-reviews{flex:1;display:flex;flex-direction:column;align-items:center}.recommend{font-size:1.3rem;color:var(--color-grey-dark-3);display:flex;align-items:center}.recommend__count{margin-right:auto}.recommend__friends{display:flex}.recommend__photo{box-sizing:content-box;height:4rem;width:4rem;border-radius:50%;border:3px solid #fff}.recommend__photo:not(:last-child){margin-right:-2rem}.review{background-color:#fff;box-shadow:var(--shadow-light);padding:3rem;margin-bottom:3.5rem;position:relative;overflow:hidden}@media only screen and (max-width: 56.25em){.review{padding:2rem;margin-bottom:3rem}}.review__text{margin-bottom:2rem;z-index:10;position:relative}.review__user{display:flex;align-items:center}.review__photo{height:4.5rem;width:4.5rem;border-radius:50%;margin-right:1.5rem}.review__user-box{margin-right:auto}.review__user-name{font-size:1.1rem;font-weight:600;text-transform:uppercase;margin-bottom:.4rem}.review__user-date{font-size:1rem;color:var(--color-grey-dark-3)}.review__rating{color:var(--color-primary);font-size:2.2rem;font-weight:300}.review::before{content:"“";position:absolute;top:-2.75rem;left:-1rem;font-size:20rem;line-height:1;color:var(--color-grey-light-2);font-family:sans-serif;z-index:1}.cta{padding:3.5rem 0;text-align:center}@media only screen and (max-width: 56.25em){.cta{padding:2.5rem 0}}.cta__book-now{font-size:1.7rem;font-weight:300;text-transform:uppercase;margin-bottom:2.5rem}